@charset "UTF-8";
$font-family:
"Microsoft YaHei",
"Helvetica Neue",
"SimHei",
Helvetica,
STHeiTi,
sans-serif;
html {

  font-family: $font-family;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body,
dl,
dd,
p,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
pre {
    margin: 0;
}

button,
input,
legend,
menu,
option,
th,
td,
textarea {
    padding: 0;
}

input[type="radio"],
input[type="checkbox"] {
    margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
    display: block;
}

audio,
canvas,
progress,
video {
    display: inline-block;
    vertical-align: baseline;
}

audio:not([controls]) {
    display: none;
    height: 0;
}

[hidden],
template {
    display: none;
}

a {
    background-color: transparent;
    text-decoration: none;
}

a:active,
a:hover {
    outline: 0;
}

abbr[title] {
    border-bottom: 1px dotted;
}

b,
strong {
    font-weight: bold;
}

dfn {
    font-style: italic;
}

h1 {
    font-size: 2em;
    margin: 0.67em 0;
}

mark {
    background: #ff0;
    color: #000;
}

small {
    font-size: 80%;
}

sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}

img {
    border: 0;
}

svg:not(:root) {
    overflow: hidden;
}

figure {
    margin: 1em 40px;
}

hr {
    box-sizing: content-box;
    height: 0;
}

pre {
    overflow: auto;
}

code,
kbd,
pre,
samp {
    font-family: $font-family;
    font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
    color: inherit;
    font-family: $font-family;
    margin: 0;
}

button {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}

button[disabled],
html input[disabled] {
    cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

input {
    line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
     -webkit-user-modify: read-write-plaintext-only;
    box-sizing: border-box;
    padding: 0;
    ::selection {
        background: none;
    }
    ::-moz-selection {
        background: none;
    }
    ::-webkit-selection {
        background: none;
    }
    &:focus {
        border: none;
        outline: none;
    }
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

input[type="search"] {
    -webkit-appearance: textfield;
    box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

fieldset {
    border: 1px solid silver;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

legend {
    border: 0;
    padding: 0;
}

textarea {
    overflow: auto;
}

optgroup {
    font-weight: bold;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

td,
th {
    padding: 0;
}

ol,
ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    border: 0;
}

* {
    box-sizing: border-box;
}

input,
textarea {
    -webkit-appearance: none;
}
/* 去除虚线框 ie6 ie7不支持此属性 */
button:focus,input:focus,select:focus,textarea:focus,a,div:focus {
  outline:none;
}
button,
input {
  *overflow: visible;
  line-height: normal;
}
input,
textarea,select{
  border: 0;
  resize:none;
   
  // &:focus{
  //   border: none;
  // }
  outline: 0;
   &::-webkit-input-placeholder {
       /* WebKit browsers */
       color: #d3d3d3;
       // @extend %fz12;
   }
   &:-moz-placeholder {
       /* Mozilla Firefox 4 to 18 */
       color: #d3d3d3;
       // @extend %fz12;
   }
   &::-moz-placeholder {
       /* Mozilla Firefox 19+ */
       color: #d3d3d3;
       // @extend %fz12;
   }
   &:-ms-input-placeholder {
       /* Internet Explorer 10+ */
       color: #d3d3d3;
       // @extend %fz12;
   }

}


/* 去掉点击时背景颜色 */

a,
button,
input {
    -webkit-tap-highlight-color: transparent;
}

a,
button,
input,
textarea {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
   
}
*{
  box-sizing:border-box;
}